import React, { Component} from "react";
import { Container, Media, Row, Col, Card, ListGroup } from "react-bootstrap";

export default class Blog extends Component {
    render() {
        return (
            <Container>
                <Row>
                    <Col md="9">
                    <img
                            className="d-block w-100 mr-3"
                            width={450}
                            height={400}
                            src="https://media.kasperskydaily.com/wp-content/uploads/sites/90/2016/04/06042315/big_data_sells_people-featured.jpg"
                            
                            />
                        <Media className="mr-5" style={{ flex:1, flexDirection:'row'}}>
                        
                            <Media.Body align = "left">
                                <b><h5 size="+200" face="Georgia">
                                Торговля людьми: как большие данные сделали из нас с вами товар на продажу
                                </h5></b>
                                <p>
                                Основная валюта нашего времени — это информация. Данные покупают и продают, воруют и искажают, а также, само собой, собирают всеми возможными способами. Чтобы узнать что-нибудь интересное о человеке, вовсе не нужно обращаться к незаконным методам — для этого в большинстве случаев хватит открытой информации.
                                </p>
                                <p>
                                С помощью специальных программ несложно проанализировать сведения, которые люди публикуют в социальных сетях в открытом доступе. Давайте разберемся, чего можно добиться с их помощью.
                                    </p>    
                            </Media.Body>
                        </Media>
                        
                        <Media className="mr-5">
                
                            <Media.Body>
                                <b><h5 size="+200" face="Georgia">
                                Связанные одной сетью
                                </h5></b>
                                <p>
                            Публикуя какие-нибудь сведения о себе онлайн, мы слабо представляем, какую картинку можно составить, сложив все кусочки пазла. Например, существует сервис, который позволяет визуализировать то, с кем вы знакомы в Сети.

На скриншоте внизу представлены два круга «друзей» в соцсети: мой и моей сестры. Каждая точка — это человек, который связан или со мной, или с ней. Что интересно, среди наших онлайн-друзей нашлись люди, которые знают друг друга и одну из нас, но не нас обеих.
                                </p>
                                <p>
                                Если потратить некоторое время на изучение схемы, можно выделить круг друзей интересующего вас человека, так как он и его компания будут связаны между собой. Так, например, выглядит мой круг знакомств с прошлого места работы.
                                    </p>    
                            </Media.Body>
                            
                        </Media>
                        <img
                            className="d-block w-100 mr-3"
                            width={500}
                            height={400}
 
                            src="https://media.kasperskydaily.com/wp-content/uploads/sites/90/2016/04/06035621/ru-1-1024x425.jpg"
                            
                            />
                        <Media className="mr-5">
                
                            <Media.Body>
                                <p>
                                Помните пословицу «Скажи мне, кто твой друг, и я скажу, кто ты»? Она работает. Если большинство людей в одном маленьком кругу объединяет одно увлечение, значит, и тот человек из этого круга, который вас интересует, с большой долей вероятности будет интересоваться тем же. Если все контакты указали в качестве места работы одну и ту же компанию, то вы, скорее всего, обнаружили строчку из резюме этого человека.

                                При должном желании можно быстро выделить наиболее вероятных бывших и настоящих поклонников заинтересовавшего вас человека, найти его коллег, семью и друзей, даже если они не указаны в его профиле, и узнать много других сведений о нем самом.
                                </p>   
                            </Media.Body>
                            
                        </Media>    
                        <img
                            className="d-block w-100 mr-3"
                            width={500}
                            height={400}

                            src="https://media.kasperskydaily.com/wp-content/uploads/sites/90/2016/04/06042319/big_data_2.jpg"
                            
                            />
                    </Col>
                    <Col md="3">
                        <h5 className=" text-center mr-5">Категория</h5>
                        <Card>
                            <ListGroup variant="flush">
                                <ListGroup.Item>Защита информации</ListGroup.Item>
                            </ListGroup>
                            <ListGroup variant="flush">
                                <ListGroup.Item>Киберпреступления</ListGroup.Item>
                            </ListGroup>
                        </Card>
                    </Col>
                </Row>
            </Container>

        )
    }
}